<template>
  <div class="base-show-all-row-container f-row-between">

    <div class="title f-i-c">
      <div class="shading" />
      <p class="row-title">{{ rowTitle }}</p>
      <p v-if="albumInfo" class="update-status">
        {{ albumInfo.isEndUpdate ? `${albumInfo.totalItemCount}节全` : `更新至${albumInfo.itemCount}节` }}
      </p>
    </div>
    
  </div>
</template>

<script>
export default {
  props: {
    rowTitle: {
      type: String,
      default: '无标题'
    },
    albumInfo: {
      type: Object,
      default: null
    }
  },
}
</script>

<style lang="scss" scoped>
.base-show-all-row-container {
  width: 100%;
  height: 25px;

  .title {
    width: 80%;
    font-size: 18px;
    font-weight: bold;
    color: rgba(51,51,51,1);
    position: relative;

    .shading {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 45px;
      height: 8px;
      background: linear-gradient(90deg,rgba(244,186,65,1) 0%,rgba(255,255,255,1) 100%);
    }
    .row-title {
      position: relative;
    }
    .update-status {
      margin-left: 15px;
      font-size: 13px;
      color: rgba(170,170,170,1);
      font-weight: 400;
    }
  }
}
</style>


