<template>
  <div v-if="albumInfo" class="album-introduction-container">
    
    <AlbumHeader
      class="album-header-box"
      :albumInfo="albumInfo"
    />

    <div class="split-div-1" />
    
    <div class="manuscript">
      <AlbumCommentTitle
        @clickSeeWonderComment="$router.push( '/detail-message' )"
        rowTitle="介绍"
        :albumInfo="albumInfo"
      />
      <div
        v-html="albumInfo.desc"
        class="rich-text"
      />
    </div>

    <div
      class="button-hear-box"
    >
      <van-button
        v-if="!albumIsNeedBuy(albumInfo.chargeType, albumInfo.isBuy)"
        class="button-hear"
        type="default"
        text="前往收听"
        round
        color="linear-gradient(90deg,rgba(246,197,67,1),rgba(239,140,56,1))"
        @click="listenAlbum"
      />
      <div
        v-else
        class="not-buy-box f-row-between"
      >
        <div @click="listenAlbum" class="not-buy f-row-between-c">
          <img src="@/assets/images/album_listen.png" alt="">
          <span>免费试听</span>
        </div>
        <van-button
          class="button-join"
          type="default"
          :text="buyText"
          round
          color="linear-gradient(90deg,rgba(246,197,67,1),rgba(239,140,56,1))"
          @click="buyAlbum"
        />
      </div>
    </div>

    <AlbumBuyPopup
      :isShowBuyPopup="isShowBuyPopup"
      @closeBuyPopup="closeBuyPopup"
      @clickConfirmBuy="clickConfirmBuy"
      :albumInfo="albumInfo"
    />

    <div class="bottom-div-1" />

  </div>
</template>

<script>
import AlbumHeader from '@/components/album/AlbumHeader.vue';
import AlbumCommentTitle from '@/components/album/AlbumCommentTitle.vue';
import AlbumBuyPopup from '@/components/album/AlbumBuyPopup';
import { apiAlbumInfo } from '@/service/api/program';
import { apiOrderPrePay, apiOrderSubmit } from '../../service/api/payment';
import { toDecimal2 } from '../../utils/money';
import { albumIsNeedBuy } from '../../utils/index';
import Vue from 'vue';
import { Button, Toast } from 'vant';
import wx from 'weixin-js-sdk';

Vue.use(Button).use(Toast);

export default {
  components: {
    AlbumHeader,
    AlbumCommentTitle,
    AlbumBuyPopup
  },

  data() {
    return {
      manuscript: '<p style="color: #333; line-height: 2">在孩子的负面情绪面前，父母不时会陷入崩溃，不知道到底该如何管教孩子。一方面怕管教太严，孩子会逆反；另一方面又怕不管教，孩子难以形成好的习惯和性格。这就导致我们有情绪也不敢表达，总是压抑自己。国际著名亲子沟通专家阿黛尔·法伯和伊莱恩·玛兹丽施告诉我们，面对孩子的负面情绪，父母需要的是方法，而不是情绪。通过大量、长期的家庭教育实践，作者总结出一系列清晰有效的方法，帮助我们正确识别、处理孩子的负面情绪，让孩子学会与他的负面情绪共处；同时帮助父母解放自己的育儿焦虑和负面情绪，从而拥有一个轻松愉快的家庭氛围。这本书的作者是国际著名亲子沟通专家阿黛尔·法伯和伊莱恩·玛兹丽施。她们因为对美国家庭教育做出巨大贡献，入选了“美国名人录”。两位作者在1980年所著的《如何说孩子才会听，怎么听孩子才肯说》一书，被誉为“父母与孩子沟通的圣经”，被翻译为30多种语言，位居美国亲子教育书籍排行榜榜首长达二十多年。这本《解放父母，解放孩子》是两位作者出版的第一本书，自1974年出版至今已经畅销40多年。很少有家教书能拥有这么强的生命力，一个重要的原因在于，这本书提供的方法仍不过时。</p>',
      hasOwner: true,
      albumInfo: null,
      buyText: '',
      isShowBuyPopup: false
    }
  },

  created() {
    document.title = '青豆父母学堂';
  },

  mounted() {
    this.getAlbumInfo( this.$route.query.albumId );
  },

  methods: {
    // todo 判断用户是否需要购买当前专辑
    albumIsNeedBuy(chargeType, isBuy) {
      return albumIsNeedBuy(chargeType, isBuy);
    },

    closeBuyPopup() {
      this.isShowBuyPopup = false;
    },
    clickConfirmBuy() {
      this.isShowBuyPopup = false;
      this.buyAlbum();
    },

    // todo 购买逻辑
    buyAlbum() {
      this.openNewOrder(); // 购买流程
    },
    async openNewOrder() {
      let spus = [{
        spuId: this.albumDetail.spuInfo.spuId,
        skuId: '',
        quantity: '1',
        spuName: this.albumDetail.name
      }];

      const newOrderResponse = await apiOrderSubmit({ spus });
      if (!newOrderResponse || newOrderResponse.type === 'fail') return Toast( '服务器错误，请联系客服' );
      console.log('newOrderResponse: ', newOrderResponse);

      this.launchWechatPay(newOrderResponse.data.orderNo);
    },
    async launchWechatPay(orderNumber) {
      const prePayResponse = await apiOrderPrePay( orderNumber );
      if (!prePayResponse || prePayResponse.type === 'fail') return Toast( '服务器错误，请联系客服' );

      let wechatConfig = prePayResponse.data;
      wechatConfig.jsApiList = ['chooseWXPay'];
      wechatConfig.timestamp = wechatConfig.timeStamp;
      console.log('wechatConfig: ', wechatConfig, typeof wechatConfig);

      try {
        wx.config(wechatConfig);
      } catch (error) {
        console.log('error: ', error);
      }

      wx.ready(function () {
        wx.chooseWXPay(...{
          wechatConfig,
          success: function(res) {
            // 支付成功后的回调函数
            alert("支付成功:");
          },
          error: function() {
            alert("支付失败");
          },
          cancel: function() {
            alert("支付取消");
          },
          complete: function() {
            alert("支付完成");
          },
        });
      });
    },

    // todo 获取专辑信息
    async getAlbumInfo(albumId) {
      const albumResponse = await apiAlbumInfo(albumId);
      if (!albumResponse || albumResponse.type === 'fail') return Toast( '服务器错误，请联系客服' );
      console.log('albumResponse: ', albumResponse);
      this.albumInfo = albumResponse.data;
      this.buyText = '￥' + toDecimal2(this.albumInfo.spuInfo.price) + ' 购买';
    },

    listenAlbum() {
      this.$router.push({
        path: '/album-catalogue',
        query: {
          albumId: this.$route.query.albumId
        }
      });
      
      // this.openNewOrder(); // 购买流程
    },
    async openNewOrder() {
      let spus = [{
        spuId: this.albumInfo.spuInfo.spuId,
        skuId: '',
        quantity: '1',
        spuName: this.albumInfo.name
      }];

      const newOrderResponse = await apiOrderSubmit({ spus });
      if (!newOrderResponse || newOrderResponse.type === 'fail') return Toast( '服务器错误，请联系客服' );
      console.log('newOrderResponse: ', newOrderResponse);

      this.launchWechatPay(newOrderResponse.data.orderNo);
    },
    async launchWechatPay(orderNumber) {
      const prePayResponse = await apiOrderPrePay( orderNumber );
      if (!prePayResponse || prePayResponse.type === 'fail') return Toast( '服务器错误，请联系客服' );
      console.log('prePayResponse: ', prePayResponse.data);

      let wechatConfig = prePayResponse.data;
      wechatConfig.jsApiList = ['chooseWXPay'];
      wechatConfig.timestamp = wechatConfig.timeStamp;
      try {
        wx.config(wechatConfig);
      } catch (error) {
        console.log('error: ', error);
      }

      wx.ready(function () {
        wx.chooseWXPay(wechatConfig);
      });
    }
  }
}
</script>

<style lang="scss" scoped>
.album-introduction-container {
  width: 100%;
  background: rgba(246,246,246,1);

  .album-header-box {
    width: 100%;
    height: 160px;
    box-sizing: border-box;
    padding: 20px 16px;
    background-color: #fff;
  }

  .split-div-1 {
    width: 100%;
    height: 10px;
  }

  .manuscript {
    width: 100%;
    box-sizing: border-box;
    padding: 20px 16px;
    background-color: #fff;

    .rich-text {
      margin-top: 25px;
    }
  }

  .button-hear-box {
    position: fixed;
    bottom: 0;
    width: 100%;
    padding: 10px 20px 10px;
    background: #fff;
    z-index: 1;

    .button-hear {
      width: 100%;
      font-size: 16px;
    }
    .not-buy-box {
      
      .not-buy {
        width: 88px;
        margin-left: 15px;
        
        img {
          width: 18px;
          height: 18px;
        }
        span {
          font-size: 15px;
          font-weight: bold;
          color: rgba(244,186,65,1);
        }
      }
      .button-join {
        width: 157.5px;
      }
    }
  }

  .bottom-div-1 {
    width: 100%;
    height: 65px;
    background-color: #fff;
  }
}
</style>


