<template>
  <div v-if="albumInfo" class="album-buy-popup-container">
    <van-popup
      v-model="isShowBuyPopup"
      @close="$emit('closeBuyPopup')"
      style="border-radius: 5px"
    >
      <div class="buy-box f-col-i-c">
        <p class="title">
          {{ albumInfo.name }}
        </p>
        <div
          class="cover"
          :style="{ 'background-image': `url( ${albumInfo.cover} )` }"
        />
        <p v-if="albumInfo.spuInfo" class="price">
          ¥{{ albumInfo.spuInfo.price | fltToDecimal2 }}
        </p>
        <div class="button-box flx">
          <van-button
            @click="$emit('closeBuyPopup')"
            class="cancel"
            text="取消"
          />
          <van-button
            @click="$emit('clickConfirmBuy')"
            class="confirm"
            text="确认购买"
          />
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import Vue from 'vue';
import { Popup, button } from 'vant';

Vue.use(Popup).use(button);

export default {
  props: {
    isShowBuyPopup: {
      type: Boolean,
      default: false
    },
    albumInfo: {
      type: Object,
      default: null
    }
  },

  data() {
    return {
      
    }
  }
}
</script>

<style lang="scss" scoped>
.album-buy-popup-container {
  width: 100%;

  .buy-box {
    width: 280px;
    height: 296px;
    padding: 30px 28px 0;
    background: rgba(255,255,255,1);
    border-radius: 5px;

    .title {
      text-align: center;
      width: 100%;
      font-size: 16px;
      color: rgba(0,0,0,1);
      line-height: 1.5;
    }
    .cover {
      margin-top: 10px;
      width: 90px;
      height: 120px;
      background: rgba(242,242,242,1);
      box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.2);
      border-radius: 5px;
      background-position: center center;
      background-size: cover;
      background-repeat: no-repeat;
    }
    .price {
      text-align: center;
      margin-top: 10px;
      width: 100%;
      font-size: 15px;
      font-weight: 600;
      color: rgba(239,60,60,1);
    }
    .button-box {
      width: 100%;
      position: absolute;
      bottom: 0;

      .cancel, .confirm {
        width: 50%;
      }
      .confirm {
        color: #F4BA41;
      }
      .van-button {
        border-radius: none;
      }
    }
  }
}
</style>


